.navbar {
    background-color: var(--color-surface);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    position: sticky;
    top: 0;
    z-index: 50;
}

.navbar-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 5rem; /* 80px */
}

.logo {
    display: flex;
    align-items: center;
}

.logo-icon {
    height: 2rem; /* 32px */
    width: 2rem;
    color: var(--color-primary);
}

.logo-text {
    margin-left: 0.75rem;
    font-size: 1.25rem;
    font-weight: bold;
    color: var(--color-text);
}

.logo-text-light {
    font-weight: 300;
}

.nav-links {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.nav-item {
    display: flex;
    align-items: center;
    padding: 0.5rem 0.75rem;
    border-radius: 0.375rem;
    font-size: 0.875rem;
    font-weight: 500;
    color: var(--color-text-muted);
    transition: color 0.2s, background-color 0.2s;
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-family: inherit;
    text-align: left;
    white-space: nowrap;
}

.nav-item:hover {
    background-color: var(--color-border);
    color: var(--color-primary);
}

.nav-item-label {
    margin-left: 0.5rem;
}

.nav-divider {
    width: 1px;
    height: 24px;
    background-color: var(--color-border);
    margin: 0 0.5rem;
}

/* Dropdown Styles */
.nav-item-dropdown-container {
    position: relative;
}

.dropdown-menu {
    position: absolute;
    top: 100%;
    background-color: var(--color-surface);
    border: 1px solid var(--color-border);
    border-radius: 0.5rem;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
    margin-top: 0.5rem;
    padding: 0.5rem;
    z-index: 60;
    width: max-content;
    animation: fade-in 0.2s ease-out;
}

.dropdown-menu-right {
    right: 0; /* Aligns the dropdown to the right */
    left: auto;
}

.dropdown-item {
    display: flex; /* Use flexbox for icon alignment */
    align-items: center;
    width: 100%;
    text-align: left;
    padding: 0.5rem 1rem;
    font-size: 0.875rem;
    color: var(--color-text-muted);
    background-color: transparent;
    border: none;
    border-radius: 0.375rem;
    cursor: pointer;
    white-space: nowrap;
}

.dropdown-item:hover {
    background-color: var(--color-border);
    color: var(--color-primary);
}

.dropdown-item-icon {
    margin-right: 0.75rem;
}
